import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NodeWithI18n } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { ReceptionComponent } from 'src/app/apps/colis/reception/reception.component';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';


interface ReceptionDTO {
	
	 reference?,	
	 name?,	
	 type?,
	 adresse?,
	 email?,
	
	 nomsender?,
	 telexpediteur?,	
	 namerecipient?,
	 telrecipient?,	
	 tel2?,
	 typearticle?,
   datereception?,

   dommage?,
   commentaire?,
   envoisms?,

   paysexpediteur?,
   paysrecipient?,


}

@Component({
  selector: 'app-nouveau-colisreception',
  templateUrl: './nouveaucolisreception.component.html',
  styleUrls: ['./nouveaucolisreception.component.css'],
  providers:[MessageService]
})
export class NouveaucolisreceptionComponent implements OnInit {

  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


  /**
   * Variable
   */
    typeactivite:Dropdown;
    typeactivites : Dropdown [] = [] ;

    msgs:Message[] = []; 
    colisForm: FormGroup; 

    typearticle = 'COLIS - CP';
    disabled : boolean = true;

    receptiondto :ReceptionDTO  = {};

    dateactuel : Date = new Date();

    dommage:boolean = false;
    commentdommage:string = "N/A";

    envoisms:boolean = false;

    selectedCountryexpediteur: string;
    selectedCountrydestinateur: string;

    countries: any[];


  /**
   * Funciton ngOnInit
   */
  ngOnInit(): void {


    this.colisForm = this.formBuilder.group({
      'typearticle': new FormControl(''),
      'reference': new FormControl('', Validators.required),
      'adresse': new FormControl('', Validators.required),

      
      'nomsender': new FormControl(''),
      'telexpediteur': new FormControl(''),

      'namerecipient': new FormControl('', Validators.required),
      'telrecipient': new FormControl('', Validators.required),
      'datereception': new FormControl('', Validators.required),
      'email':new FormControl(''),
      'dommage': new FormControl(''),
      'commentaire': new FormControl('N/A', Validators.required),
      'envoisms' : new FormControl(''),
      'paysexpediteur': new FormControl(''),
      'paysdestinateur': new FormControl('')

    });

    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES TYPE D ARTICLE
     */
    this.httpClient.get(environment.url+"/api/postal/type/reception/all", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response:any=[])=>{

      this.typeactivites = [];
      response.forEach(element => {   
        
        if(element['name'] == 'COLIS - CP')
        {
          this.typeactivite = {code:element, name:element['name']};
        }
        this.typeactivites.push({code:element, name:element['name']});
      });

    }, error=>{      
      this.showWarn("Le type d'article n'a pas pu etre charg, vous pouvez continuer cela ne bloquera pas dans l'enregistrement de votre article - colis ");
    });
    
    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES PAYS
     */
    this.httpClient.get(environment.url+"/api/postal/reception/pays/all", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response:any=[])=>{

        this.countries = [];
        response.forEach(element => {   
            this.countries.push({name:element['name'], code:element['code']});
        });

      }, error=>{      
        this.showWarn("La liste des pays n'a pas pu etre charg ");
      });
  }


  /**
   * 
   * @param $event 
   */
  referenceVerify($event)
  {
    console.log($event);
  }

  /**
   * 
   * @param colisForm 
   */
  save(colisForm:any)
  {
    console.log(colisForm);

    const regex = /\d/;   
     let amontsection = colisForm['reference'].substring(0,2);
     let numbersection =  colisForm['reference'].substring(3,12);
     let avalsection = colisForm['reference'].substring(12,14);


     if( typeof(amontsection)  != 'number' || typeof(numbersection) != 'string' || typeof(avalsection)  != 'number')
     {
      this.showConfirm("Numero de reference erronn", "Une reference doit contenir deux lettre en amant suivi de 9 chiffre et avec deux lettre  la fin !!!");
    }else{

      this.httpClient.get(environment.url+"/api/postal/reception/reference?reference="+colisForm['reference'], {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe(response=>{
          if(response == null)
          {
                const format = 'yyyy-MM-dd';    
                const format_date = 'dd';
                const locale = 'en-US';

                this.receptiondto.reference = colisForm['reference'];
                this.receptiondto.name = 'COLIS - PERSONNEL';
                this.receptiondto.type = colisForm['typearticle'];
                this.receptiondto.adresse = colisForm['adresse'];
                
                this.receptiondto.nomsender = colisForm['nomsender'];
                this.receptiondto.telexpediteur = colisForm['telexpediteur'];
                
                this.receptiondto.namerecipient = colisForm['namerecipient'];
                this.receptiondto.telrecipient = colisForm['telrecipient'];
                this.receptiondto.email = colisForm['email'];
                this.receptiondto.datereception = formatDate(colisForm['datereception'],format,locale);

                this.receptiondto.typearticle = this.typeactivite.code;
                this.receptiondto.dommage = this.dommage;
                this.receptiondto.commentaire = colisForm['commentaire'];
                this.receptiondto.envoisms = this.envoisms;

                this.receptiondto.paysrecipient = this.selectedCountrydestinateur['code'];
                this.receptiondto.paysexpediteur = this.selectedCountryexpediteur['code'];
            
                
                this.httpClient.post(environment.url+"/api/postal/reception/save",this.receptiondto, {headers: new HttpHeaders({
                  'Authorization': 'Bearer '+this.tokenStorage.getToken()
                  })
                }).subscribe((response)=>{

                  this.showSuccess("Vous avez enregistrer avec success votre colis  !!! ")
                }, error=>{
                    this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre colis - La raison est voici : "+error.message);
                });
                
           
              }else{
                this.showConfirm("Numero de reference existante", "Veuillez fourni un nouveau numero de reference, car ce dernier existe !!! ");
              }
            //this.showSuccess("Vous avez enregistrer avec success votre colis  !!! ")
          }, error=>{
              this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre colis - La raison est voici : "+error.getMessage());
          });
    
        }
    
    }

  /**
   * 
   * @param $event 
   */
  dommageSelect($event)
  {
    console.log($event);    

    if($event.checked)
    {
        this.commentdommage = undefined;
    }else{
      this.commentdommage = 'N/A';
      this.colisForm.patchValue({commentaire:this.commentdommage});
    }
  }

  



  /**
   *  costumisation des erreurs
   */

  showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }


  showConfirm(title, detail) {
    this.messageService.clear();
    this.messageService.add({key: 'c', sticky: true, severity:'warn', summary:title, detail:detail});
  }

  onConfirm() {
    this.messageService.clear('c');
    this.colisForm.patchValue({reference:undefined});

  }

}
