
import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmationService, Message, MessageService } from 'primeng/api';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';


interface receptioneDTO {
	
  reference?,	
  name?,	
  type?,
  adresse?,
  email?,
 
  nomsender?,
  telexpediteur?,	
  namerecipient?,
  telrecipient?,	
  tel2?,
  typearticle?,
  datereception?,

  dommage?,
  commentaire?,
  envoisms?,

  paysexpediteur?,
  paysrecipient?,


}


@Component({
  selector: 'app-editreception',
  templateUrl: './editreception.component.html',
  styleUrls: ['./editreception.component.css'],
  providers:[MessageService, ConfirmationService]
})
export class EditreceptionComponent implements OnInit {


  constructor(private formBuilder: FormBuilder,private messageService: MessageService,  private tokenStorage:TokenStorageService,
    private httpClient: HttpClient, private router: Router, private activeroute:ActivatedRoute, private confirmationService:ConfirmationService) { }



  /**
   * Variable
   */
    typeactivite:Dropdown;
    typeactivites : Dropdown [] = [] ;

    msgs:Message[] = [];
    emsForm: FormGroup; 

    //typearticle = 'EMS - EE';
    disabled : boolean = true;

    receptiondto :receptioneDTO  = {};

    private value = undefined;
    private datereception : Date ;

    

    dateactuel : Date = new Date();

    dommage:boolean = false;
    commentdommage:string = "N/A";

    envoisms:boolean = false;

    selectedCountryexpediteur: any;
    selectedCountrydestinateur: any;

    countries: any[];




  /**
   * Funciton ngOnInit
   */
  ngOnInit(): void {

    this.activeroute.queryParams
    .subscribe(params =>{
      this.value=params.id;
      
    });

    this.emsForm = this.formBuilder.group({
      'typearticle': new FormControl(''),
      'reference': new FormControl('', Validators.required),
      'adresse': new FormControl('', Validators.required),

      
      'nomsender': new FormControl(''),
      'telexpediteur': new FormControl(''),

      'namerecipient': new FormControl('', Validators.required),
      'telrecipient': new FormControl('', Validators.required),
      'datereception': new FormControl('', Validators.required),
      'email':new FormControl(''),
      'dommage': new FormControl(''),
      'commentaire': new FormControl('N/A', Validators.required),
      'envoisms' : new FormControl(''),
      'paysexpediteur': new FormControl(''),
      'paysdestinateur': new FormControl('')

    });

    const format = 'dd-MM-yyyy';    
    const format_date = 'dd';
    const locale = 'en-US';

    const date = 'dd';
    const mont= 'MM';
    const year = 'yyyy';

     const reponse = this.httpClient.get(environment.url+"/api/postal/reception/edit?gkey="+this.value, {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(
      response=>{

        console.log(response);
          this.receptiondto.reference = response['reference'];
          this.receptiondto.type = response['type'];
          this.receptiondto.adresse = response['adresse'];
          this.dommage = response['dommage'];
          this.envoisms = response['envoisms'];
          this.datereception = new Date(response['datereception']);
          console.log(this.datereception);

          this.receptiondto.nomsender = response['nomsender'];
          this.receptiondto.telexpediteur = response['telexpediteur'];          
          this.receptiondto.namerecipient = response['namerecipient'];
          this.receptiondto.telrecipient = response['telrecipient'];

          this.receptiondto.dommage = this.dommage;
          this.receptiondto.commentaire = response['commentaire'];
          this.receptiondto.envoisms = this.envoisms;
          /*
          this.receptiondto.paysrecipient = this.selectedCountrydestinateur['code'];
          this.receptiondto.paysexpediteur = this.selectedCountryexpediteur['code'];
          */

          this.httpClient.get(environment.url+"/api/postal/reception/pays/all", {headers: new HttpHeaders({
            'Authorization': 'Bearer '+this.tokenStorage.getToken()
            })
          }).subscribe((response2:any=[])=>{
              console.log(response2)
              this.countries = [];
              response2.forEach(element => {   
    
                  if(element['code'] == response['paysexpediteur'])
                  {
                    console.log(" je suis i dans selectedCountry expediteur");
                    this.selectedCountryexpediteur = {name:element['name'], code:element['code']} ;
                  }
    
                  if(element['code'] ==  response['paysrecipient'])
                  {
                    console.log(" je suis i dans selectedCountry destinateur");
                    this.selectedCountrydestinateur = {name:element['name'], code:element['code']} ;
                  }
    
                  this.countries.push({name:element['name'], code:element['code']});
              });

          }, error=>{      
            this.showWarn("La liste des pays n'a pas pu etre charg ");
          });

      }, error=>{
        this.showWarn("L'article a modifi n'a pas pu etre charg, Voici la raison "+error.message);
      });

           
    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES PAYS
     */
    Promise.all([reponse]).then( ()=>{
      

    })
    


  }

  save(emsForm:any)
  {
    console.log(emsForm);

    if( this.receptiondto.reference == emsForm['reference'])
    {
      const format = 'yyyy-MM-dd';   
      const format_date = 'dd';
      const locale = 'en-US';


      this.receptiondto.reference = emsForm['reference'];
      
      this.receptiondto.type = emsForm['typearticle'];
      this.receptiondto.adresse = emsForm['adresse'];
      
      this.receptiondto.nomsender = emsForm['nomsender'];
      this.receptiondto.telexpediteur = emsForm['telexpediteur'];
      
      this.receptiondto.namerecipient = emsForm['namerecipient'];
      this.receptiondto.telrecipient = emsForm['telrecipient'];
      this.receptiondto.email = emsForm['email'];
      this.receptiondto.datereception = formatDate(this.datereception, format, locale);

      this.receptiondto.dommage = this.dommage;
      this.receptiondto.commentaire = emsForm['commentaire'];
      this.receptiondto.envoisms = this.envoisms;

      this.receptiondto.paysrecipient = this.selectedCountrydestinateur['code'];
      this.receptiondto.paysexpediteur = this.selectedCountryexpediteur['code'];

      console.log(this.receptiondto);

      const reponse = this.httpClient.post(environment.url+"/api/postal/reception/edit?id="+this.value,this.receptiondto, {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe((response)=>{

        this.showSuccess("L'article a t mise  jour avec success !!! ")
      }, error=>{
          this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre EMS - La raison est voici : "+error.message);
      });


    }else{    
        this.httpClient.get(environment.url+"/api/postal/reception/reference?reference="+emsForm['reference'], {headers: new HttpHeaders({
          'Authorization': 'Bearer '+this.tokenStorage.getToken()
          })
        }).subscribe(response=>{
            if(response == null)
            {
                  const format = 'yyyy-MM-dd';   
                  const format_date = 'dd';
                  const locale = 'en-US';


                  this.receptiondto.reference = emsForm['reference'];
                  
                  this.receptiondto.type = emsForm['typearticle'];
                  this.receptiondto.adresse = emsForm['adresse'];
                  
                  this.receptiondto.nomsender = emsForm['nomsender'];
                  this.receptiondto.telexpediteur = emsForm['telexpediteur'];
                  
                  this.receptiondto.namerecipient = emsForm['namerecipient'];
                  this.receptiondto.telrecipient = emsForm['telrecipient'];
                  this.receptiondto.email = emsForm['email'];
                  this.receptiondto.datereception = formatDate(this.datereception, format, locale);

                  this.receptiondto.dommage = this.dommage;
                  this.receptiondto.commentaire = emsForm['commentaire'];
                  this.receptiondto.envoisms = this.envoisms;

                  this.receptiondto.paysrecipient = this.selectedCountrydestinateur['code'];
                  this.receptiondto.paysexpediteur = this.selectedCountryexpediteur['code'];

                  console.log(this.receptiondto);

                  const reponse = this.httpClient.post(environment.url+"/api/postal/reception/edit?id="+this.value,this.receptiondto, {headers: new HttpHeaders({
                    'Authorization': 'Bearer '+this.tokenStorage.getToken()
                    })
                  }).subscribe((response)=>{

                    this.showSuccess("L'article a t mise  jour avec success !!! ")
                  }, error=>{
                      this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre EMS - La raison est voici : "+error.message);
                  });
                  
            }else{
              this.showConfirm("Numero de reference existante", "Veuillez fourni un nouveau numero de reference, car ce dernier existe !!! ");
            }
            }, error=>{
              this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre colis - La raison est voici : "+error.getMessage());
        });

    }

}

   
  

    /**
   * 
   * @param $event 
   */
     dommageSelect($event)
     {
       console.log($event);    
   
       if($event.checked)
       {
           this.commentdommage = undefined;
       }else{
         this.commentdommage = 'N/A';
         this.emsForm.patchValue({commentaire:this.commentdommage});
       }
     }
   
  
  /**
   *  costumisation des erreurs
   */

   showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }

  
  showConfirm(title, detail) {
    this.messageService.clear();
    this.messageService.add({key: 'c', sticky: true, severity:'warn', summary:title, detail:detail});
  }

  onConfirm() {
    this.messageService.clear('c');
    this.emsForm.patchValue({reference:undefined});

  }

}
