import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';


interface EnvoieDTO {
	
	 reference?,	
	 name?,	
	 type?,
	 adresse?,
	 email?,
	
	 nomsender?,
	 telexpediteur?,	
	 namerecipient?,
	 telrecipient?,	
	 tel2?,
	 typearticle?,

}

@Component({
  selector: 'app-nouveau-recommande',
  templateUrl: './nouveaurecommande.component.html',
  styleUrls: ['./nouveaurecommande.component.css'],
  providers:[MessageService]
})
export class NouveaurecommandeComponent implements OnInit {

  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


  /**
   * Variable
   */
    typeactivite:Dropdown;
    typeactivites : Dropdown [] = [] ;

    msgs:Message[] = [];
    recommandeForm: FormGroup; 

    typearticle = 'RECOMMANDE - RR';
    disabled : boolean = true;

    envoidto :EnvoieDTO  = {};
  /**
   * Funciton ngOnInit
   */
  ngOnInit(): void {


    this.recommandeForm = this.formBuilder.group({
      'typearticle': new FormControl(''),
      'reference': new FormControl('', Validators.required),
      'adresse': new FormControl('', Validators.required),

      
      'nomsender': new FormControl('', Validators.required),
      'telexpediteur': new FormControl('', Validators.required),

      'namerecipient': new FormControl('', Validators.required),
      'telrecipient': new FormControl('', Validators.required),

    });

    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES TYPE D ARTICLE
     */
    this.httpClient.get(environment.url+"/api/postal/type/activite/all", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response:any=[])=>{

      this.typeactivites = [];
      response.forEach(element => {   
        
        if(element['name'])
        {
          this.typeactivite = {code:element, name:element['name']};
        }
        this.typeactivites.push({code:element, name:element['name']});
      });

    }, error=>{      
      this.showWarn("Le type d'article n'a pas pu etre charg, vous pouvez continuer cela ne bloquera pas dans l'enregistrement de votre article - recommande ");
    });

  }

  save(recommandeForm:any)
  {
   
    let amontsection = recommandeForm['reference'].substring(0,2);
    let numbersection = Number(recommandeForm['reference'].substring(2,11));
    let avalsection = recommandeForm['reference'].substring(11,14);
    let letters = /^[A-Za-z]+$/;

    if( !amontsection.match(letters) || amontsection.length !=2|| !numbersection || !avalsection.match(letters) || avalsection.length !=2)
    {
      this.showConfirm("Numero de reference erronn", "Une reference doit contenir deux lettre en amant suivi de 9 chiffre et avec deux lettre  la fin !!!");
    }else{

      this.httpClient.get(environment.url+"/api/postal/envoi/reference?reference="+recommandeForm['reference'], {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe(response=>{
          if(response == null)
          {
              this.envoidto.reference = recommandeForm['reference'];
              this.envoidto.name = 'COLIS RECOMMANDE';
              this.envoidto.type = recommandeForm['typearticle'];
              this.envoidto.adresse = recommandeForm['adresse'];
              
              this.envoidto.nomsender = recommandeForm['nomsender'];
              this.envoidto.telexpediteur = recommandeForm['telexpediteur'];
              
              this.envoidto.namerecipient = recommandeForm['namerecipient'];
              this.envoidto.telrecipient = recommandeForm['telrecipient'];

              this.envoidto.typearticle = this.typeactivite.code;
              

              this.httpClient.post(environment.url+"/api/postal/envoi/save",this.envoidto, {headers: new HttpHeaders({
                'Authorization': 'Bearer '+this.tokenStorage.getToken()
                })
              }).subscribe((response)=>{

                this.showSuccess("Vous avez enregistrer avec success votre recommande  !!! ")
              }, error=>{
                  this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre recommande - La raison est voici : "+error.getMessage());
              });
          }else{
            this.showConfirm("Numero de reference existante", "Veuillez fourni un nouveau numero de reference, car ce dernier existe !!! ");
          }
          //this.showSuccess("Vous avez enregistrer avec success votre colis  !!! ")
      }, error=>{
        this.showError(" une erreur c'est produit et le systme n'a pas enregitr votre colis - La raison est voici : "+error.getMessage());
      });

}

}



  /**
   *  costumisation des erreurs
   */

  showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }

  
  showConfirm(title, detail) {
    this.messageService.clear();
    this.messageService.add({key: 'c', sticky: true, severity:'warn', summary:title, detail:detail});
  }

  onConfirm() {
    this.messageService.clear('c');
    this.recommandeForm.patchValue({reference:undefined});

  }


}
